import 'babel-polyfill'
import path from 'path'
import http from 'http'
import express from 'express'

import _ from 'lodash';
import cors from 'cors'
import morgan from 'morgan'
import bodyParser from 'body-parser'
import compress from 'compression'
import config from './config'
import helmet from 'helmet'
import methodOverride from 'method-override'

import passport from "passport";
import jwtStrategy from './middleware/jwtStrategy';
passport.use(jwtStrategy)

import playerRoute from './routes/player'
import agentRoute from './routes/agent'

import createAccount from './routes/createAccount';
import authLogin from './routes/authLogin'


import mongoose from 'mongoose'
import bluebird from 'bluebird'

mongoose.connect(config.mongoURL, { useMongoClient: true });
mongoose.Promise = bluebird



const app = express()
app.server = http.createServer(app)

app.use(morgan('dev'))

app.use(cors())
app.use(helmet())
app.use(compress())
app.use(methodOverride())

app.use(bodyParser.json())
app.use(bodyParser.urlencoded({ extended: true }))

app.use(passport.initialize());

app.use(express.static(path.resolve(__dirname, '../client/build')))

import setupDatabase from './admin/setupDatabase'

app.use('/admin', setupDatabase)

import updateAgentStatusAfterOrder from './updateDB/utils/updateAgentStatusAfterOrder'

app.use('/update-agent', async (req, res) => {
	await updateAgentStatusAfterOrder('59912a50a600c8ab54afebbc')
	res.json('done')
})


app.use('/api', authLogin)
app.use('/api', createAccount);

app.use('/api', passport.authenticate('jwt', {session: false}))

app.use('/api/player', playerRoute)
app.use('/api/agent',  agentRoute)

app.get('*', (request, response) => response.sendFile(path.resolve(__dirname, '../client/build', 'index.html')))

app.server.listen(process.env.PORT || config.port, () => console.log(`Started on port ${app.server.address().port}`))

export default app














